<template>
  <div class="nav">
    <i v-if="!pause" class="fa fa-pause pause-button" @click="pausePlay()"></i>
    <i v-else class="fa fa-play pause-button"  @click="pausePlay()"></i>
    <a href="https://www.linkedin.com/in/michele-carofiglio-3a30b3217"><i class="fa fa-linkedin social-icon"></i></a>
  </div>
  <div id="tiles"></div>
  <div class="container">
    <div class="name-title">
      <div>
        <h1>Michele Carofiglio</h1>
        <p>I'm a <strong>Web Developer</strong> based in Dublin with experience in 
        <strong>Full Stack</strong> development and creative <strong>UI solutions</strong>.</p>
      </div>
    </div>
    <div v-if="!toggled" class="resume-component">
      <MyResume/>
    </div>
    <div v-if="toggled" class="projects-component">
      <i class="fa fa-gear"></i>
      <h2>Hang tight, projects incoming!</h2>
    </div>
  </div>
  <Transition>
  <div v-if="!toggled" class="foot">
    <h4>click to reveal my projects&nbsp;&nbsp;&nbsp;<i class="fa fa-mouse-pointer"></i></h4>
  </div>
  </Transition>
  <Transition>
  <div v-if="toggled" class="foot">
    <h4>click to go back&nbsp;&nbsp;&nbsp;<i class="fa fa-mouse-pointer"></i></h4>
  </div>
  </Transition>
</template>

<script>
import MyResume from './components/MyResume.vue'
import anime from 'animejs/lib/anime.es.js'

export default {
  name: 'App',
  components: {
    MyResume
  },
  data() {
    return {
      toggled: false,
      pause: false,
      animation: null,
      mobile: false,
    }
  },
  mounted() {
    const wrapper = document.getElementById("tiles")

    let columns = 0,
        rows = 0

    const mobileVer = () => {
      if(document.body.clientWidth <= 767) {
        this.mobile = true
      } else {
        this.mobile = false
      }
    }
      
    const toggle = () => {
      this.toggled = !this.toggled;
      
      document.body.classList.toggle("toggled");
    }

    const handleOnClick = index => {
      toggle();
      
      anime({
        targets: ".tile",
        opacity: this.toggled ? 0 : 1,
        delay: anime.stagger(50, {
          grid: [columns, rows],
          from: index
        })
      });
    }

    const createTile = index => { // eslint-disable-line no-unused-vars
      const tile = document.createElement("div");
      tile.classList.add("tile");
      tile.style.opacity = this.toggled ? 0 : 1;
      tile.onclick = e => handleOnClick(index); // eslint-disable-line no-unused-vars
      return tile;
    }

    const createTiles = quantity => {
      Array.from(Array(quantity)).map((tile,index) => {
        wrapper.appendChild(createTile(index));
      })
    }

    const createGrid = () => {
      wrapper.innerHTML = "";

      const size = document.body.clientWidth > 800 ? 150 : 100

      columns = Math.floor(document.body.clientWidth / size);
      rows = Math.floor(document.body.clientHeight / size);

      wrapper.style.setProperty("--columns", columns)
      wrapper.style.setProperty("--rows", rows)

      createTiles(columns * rows);
    }

    createGrid();
    window.onresize = () => createGrid();
    window.onresize = () => mobileVer();

    this.animation = anime({
      targets: ".tile",
      keyframes: [
        {scale: 1},
        {scale: 1.03},
        {scale: 1},
        {scale: 0.97},
        {scale: 1}
      ],
      duration: 8000,
      direction: 'normal',
      easing: 'easeInOutSine',
      loop: true,
      delay: anime.stagger(400, {
        grid: [columns, rows],
        from: 0
      })
    });

    this.animation.play();
    window.onresize = () => this.animation.play();

  },
  methods: {
    pausePlay() {
      console.log(this.pause, this.animation)
      if(this.pause == false) {
        this.pause = true;
        this.animation.pause();
      } else {
        this.pause = false;
        this.animation.play();
      }
    },
    checkWindowSize() {

    }
  }
}
</script>

